  background-color: #ffffff;
}
.btn--white:hover, .btn--white:focus {
  color: #ffffff;
  background-color: #000000;
  outline: none;
}
.btn--white:active {
  color: #ffffff;
  background-color: #000000;
  outline: none;
  opacity: 0.5;
}
.btn--black {
  border: none;
  color: #ffffff;
  background-color: #000000;
  outline: none;
}
.btn--black:hover, .btn--black:focus {
  color: #000000;
  background-color: #ffffff;
  outline: none;
}
.btn--black:active {
  color: #000000;
  background-color: #ffffff;
  opacity: 0.5;
}

.btn-rnd {
  padding: 0;
  border: none;
  box-sizing: content-box;
  font: inherit;
  color: inherit;
  background-color: transparent;
  position: absolute;
  content: "";
  z-index: 3;
  width: 46px;
  height: 46px;
  background-origin: border-box;
  border: 1px solid #ffffff;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.4);
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.btn-rnd:active {
  outline: 0;
}
.btn-rnd:hover, .btn-rnd:focus {
  background-color: #ffffff;
  outline: none;
}
.btn-rnd:active {
  background-color: #ffffff;
  outline: none;
  opacity: 0.4;
}
.btn-rnd--prev {
  top: 50%;
  left: 0;
  display: none;
}
.btn-rnd--prev::before, .btn-rnd--prev::after {
  position: absolute;
  content: "";
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 5.5px;
  height: 1px;
  background-color: #ffffff;
  transform-origin: left;
}
.btn-rnd--prev::before {
  transform: translate(-40%, -50%) rotate(45deg);
}
.btn-rnd--prev::after {
  transform: translate(-40%, -50%) rotate(-45deg);
}
.btn-rnd--next {
  top: 50%;
  left: 100%;
}
.btn-rnd--next::before, .btn-rnd--next::after {
  position: absolute;
  content: "";
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 5.5px;
  height: 1px;
  background-color: #ffffff;
  transform-origin: right;
}
.btn-rnd--next::before {
  transform: translate(-40%, -50%) rotate(45deg);
}
.btn-rnd--next::after {
  transform: translate(-40%, -50%) rotate(-45deg);
}
.btn-rnd--prev, .btn-rnd--next {
  transition: all 0.25s ease-in-out;
}
.btn-rnd--prev:hover, .btn-rnd--prev:focus, .btn-rnd--next:hover, .btn-rnd--next:focus {
  background-color: #ffffff;
}
.btn-rnd--prev:hover::before, .btn-rnd--prev:hover::after, .btn-rnd--prev:focus::before, .btn-rnd--prev:focus::after, .btn-rnd--next:hover::before, .btn-rnd--next:hover::after, .btn-rnd--next:focus::before, .btn-rnd--next:focus::after {
  background-color: #000000;
}

.menu-overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  min-width: 300px;
  width: 100%;
  height: 0%;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  background: #3b181e;
}
