.gallery__inner {
  width: 90.1%;
  max-width: 1735px;
  margin: 0 auto;
  padding: 0 0 50px;
}

.gallery__title {
  margin-top: 10px;
  font-size: 45px;
  line-height: 50px;
  text-align: center;
  letter-spacing: 0.09em;
  text-transform: uppercase;
}

.gallery__item {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}
.gallery__item img {
  display: block;
  width: 100%;
  height: 100%;
}
.gallery__item::before {
  position: absolute;
  content: "";
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  display: none;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  opacity: 0.4;
  transition: all 0.25s ease-in-out;
}
.gallery__item:hover img {
  transition: all 0.25s ease-in-out;
  transform: scale(1.1);
}
.gallery__item:hover::before {
  display: block;
}

.gallery__image--unloaded {
  min-height: 200px;
}

@media (max-width: 767px) {
  .gallery__title {
    font-size: 25px;
    line-height: 28px;
  }
}
.modal-slider {
  width: 80vw;
  height: 80vh;
  background-color: rgba(0, 0, 0, 0.8);
}

.modal-slider__wrap {
  width: 100%;
  height: 100%;
}

.modal-slider__close-btn {
  padding: 0;
  border: none;
  box-sizing: content-box;
  font: inherit;
  color: inherit;
  background-color: transparent;
  position: absolute;
  z-index: 100;
  top: 24px;
  right: 24px;
  align-self: flex-end;
  width: 22px;
  height: 22px;
  transition: all 0.25s;
  cursor: pointer;
}
.modal-slider__close-btn:active {
  outline: 0;
}
.modal-slider__close-btn::before, .modal-slider__close-btn::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  width: 31px;
  height: 2px;
  background-color: #ffffff;
  transition: all 0.25s;
}
.modal-slider__close-btn::before {
  transform: translate(-50%, -50%) rotate(45deg);
  transform-origin: center;
}
.modal-slider__close-btn::after {
  transform: translate(-50%, -50%) rotate(-45deg);
  transform-origin: center;
}
.modal-slider__close-btn:hover::before, .modal-slider__close-btn:focus::before {
  background-color: #77cbb9;
  transform: translate(-50%, -50%) rotate(45deg) scale(1.03);
  transform-origin: center;
}
.modal-slider__close-btn:hover::after, .modal-slider__close-btn:focus::after {
  background-color: #77cbb9;
  transform: translate(-50%, -50%) rotate(-45deg) scale(1.03);
  transform-origin: center;
}
.modal-slider__close-btn:active::before, .modal-slider__close-btn:active::after {
  background-color: #2f7a6a;
}

.modal-slider__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.modal-slider__item {
  width: 100%;
  height: 100%;
}

.modal-slider__picture {
  display: block;
  width: 100%;
  height: 100%;
}
.modal-slider__picture img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}